body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  margin: 0;
  padding: 0rem;
  font-family: sans-serif;
  background: url(/img/mario-bg.png) no-repeat;
  background-size: 100%;
  background-position: bottom;
  background-color:white;
  min-height: 100%;
}
form {
  padding: 20px;
  margin-top: 60px;
}
form button, form h5 {
  margin: 20px 0;
}
input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
  border-color: #ec407a;
  box-shadow: none;
}
p {
  white-space: pre-wrap;
}
h3 {
  font-size:200%;
  color:#ff8a80;
  text-align: center;
}
.tagarea_search {
  font-size:100%;
  width:80%;
  float:left;
}
.tagarea_create {
  font-size:100%;
  width:50%;
  float:left;
}
.searh-form {
  margin-top: 0px;
}
.star {
  text-align:center;
}
.startext {
  font-size:100%;
}
.dashbord_img {
  width:100%;
  height:100%;
  margin:auto;
}
.card-image {
  width:200px;
  height:180px;
}
.card-content {
  width:inherit;
  padding: 0;
  height:100%;
}
.myprofile {
  margin:5rem 0 1rem 0;
}
.myimg {
  margin-left:0px;
  height: 100%;
  vertical-align:middle;
}
.myimg-Name {
  font-size: 25px;
}
.myprofile-card{
  box-shadow: none;
}
.book-catalog {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.profilename {
  font-size: 2rem;
  line-height: 110%;
  margin: 2.8rem 0 1.68rem;
}
.card {
  box-shadow: none;
  margin: 0rem 0 0rem
}
.collapsible-header{
  padding: 0.5rem  
}
.book-content-title {
  color:orange;
  margin-block-start: 0rem;
  font-size:150%;
}
.collapsible-body {
  padding: 0.5rem;
}
.createBookForm {
  padding:0px;
  margin-top:3px;
}
.modal {
   overflow-y: auto; 
}
.search_condition {
  float: right;
  font-size: 60%;
  margin: 0;
  color:green
}
.brand-logo{
  font-size: 150%;
}
.nav {
  font-size: 100%;
}
.catalog_summary{
  width:300px;
  height:250px;
}
.search-text {
  float: left;
  font-size:30%;
}
nav.MuiPagination-root{
  background-color: white;
  box-shadow:none
}
.collapsible {
  width: min-content;
}
.book-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.searchClear{
  margin: 10px;
}
.preloader-wrapper.big{
  left:50%;
}
.delbook{
  margin:20px 0;
}
.delfollow{
  margin:20px 0;
}
.initContent{
  padding: 10px;
  line-height: 3;
  FONT-WEIGHT:200;
}
.initialImage{
 width: 400px;
 display:block;
 margin-left:auto;
 margin-right: auto;
}
.officialAccount{
  font-size: 5px;
  padding: 0;
  margin: 0;
  color: green;
}
.officialAccount-content{
  font-size:5px;
  padding: 0;
  margin: 0;
  color:black;
}
.copy-icons{
  font-size: unset;
}
.card .card-content{
  padding:0;
}
.modal .modal-content{
   padding-bottom: 0;
}
.summery-content{
  width:265px;
}